/* eslint-disable func-names */
/* eslint-disable no-shadow */
/* eslint-disable jsx-a11y/anchor-is-valid */
/* eslint-disable react/jsx-no-bind */
/* eslint-disable react/button-has-type */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import Slide from '@material-ui/core/Slide';
import DeleteOutlineIcon from '@material-ui/icons/DeleteOutline';
import AddPhotoAlternateIcon from '@material-ui/icons/AddPhotoAlternate';
import CircularProgress from '@material-ui/core/CircularProgress';

import { toast } from 'react-toastify';
import './styles.css';
import agent from '../../../agent';
import Loader from '../../../common/Components/Loader';
import DisabledDiv from '../../../common/Components/DisabledDiv';
import TextInput from '../../../common/Components/TextInput';
import TextArea from '../../../common/Components/TextArea';
import SelectImage from '../../../common/Components/SelectImage';
import Switch from '../../../common/Components/Switch';
import Image from '../../../common/Components/Image';
import DropDownTextInput from '../../../common/Components/DropDownTextInput';
import {
  SET_PRODUCTS, GET_SHOP_DETAILS, SET_PUBLIC_PAGE_PRODUCTS,
} from '../../../constants/actionTypes';
import {
  IS_COD_AVAILABLE, IS_ONLINE_PAYMENT_AVAILABLE, ESTIMATED_DELIVERY, DELIVERY_CHARGE,
} from '../../../constants/otherConstants';

const Transition = React.forwardRef((props, ref) => <Slide direction="up" ref={ref} {...props} />);

class AdminPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      isCreateProductDialog: false,
      addImage: false,
      product: {
        images: [], cod_available: false, online_available: false,
      },
      addSizesVisible: false,
      sizes: [],
      ourCommission: { cod: { percent: 0, extra: 0 }, online: { percent: 0, extra: 0 } },
    };
    this.togelIsCreateProductDialog = this.togelIsCreateProductDialog.bind(this);
    this.deleteImage = this.deleteImage.bind(this);
    this.removeData = this.removeData.bind(this);
    this.uploadingImagesIndex = [];
    this.uploadingImagesIndexError = [];
    this.uploadingImages = [];
  }

  componentWillMount() {
    const {
      data, update, shopDetails,
    } = this.props;
    if (data) {
      this.setState({ product: { ...data, category: data.category ? data.category.name : null }, update });
      const { meta_data } = data;
      if (meta_data && meta_data.sizes_available) {
        this.setState({ sizes: meta_data.sizes_available, addSizesVisible: meta_data.sizes_available.length > 0 });
      }
    }
    if (shopDetails) {
      this.setState({ ourCommission: shopDetails.commission, categories: shopDetails.categories });
    }
  }

  componentWillReceiveProps(np) {
    const { ourCommission, categories } = this.state;
    if (np.data) {
      const { meta_data } = np.data;
      if (meta_data && meta_data.sizes_available) {
        this.setState({ sizes: meta_data.sizes_available, addSizesVisible: meta_data.sizes_available.length > 0 });
      }
      this.setState({ product: { ...np.data, category: np.data.category ? np.data.category.name : null } });
    }
    if (np.shopDetails && np.shopDetails.commission !== ourCommission) {
      this.setState({ ourCommission: np.shopDetails.commission });
    }
    if (np.shopDetails && np.shopDetails.categories !== categories) {
      this.setState({ categories: np.shopDetails.categories });
    }
  }


  onSelectFile(file, isRetry) {
    const {
      shopDetails, user, getShopDetails,
    } = this.props;
    const { product } = this.state;
    let photoFile = [];
    // this.setState({ loading: true });
    let data = {};
    let uploadingIndex = 0;
    if (isRetry) {
      data = { ...product };
      uploadingIndex = file.index;
      photoFile = [
        {
          key: 'product_id', file: product.id,
        },
        {
          key: 'photo', file: file.file.blob,
        },
      ]
    } else {
      data = { ...product, images: [...product.images, file.imageDataUrl] };
      uploadingIndex = data.images.length - 1;
      this.uploadingImages = [...this.uploadingImages, { file, index: uploadingIndex }];
      photoFile = [
        {
          key: 'product_id', file: product.id,
        },
        {
          key: 'photo', file: file.blob,
        },
      ]
    }
    // console.log("uploadingIndex",uploadingIndex, "data",data,"uplaoding images", this.uploadingImages);
    this.uploadingImagesIndex = [...this.uploadingImagesIndex, uploadingIndex];

    this.setState({ product: data });

    agent.ProductsPage.addProductImage(photoFile).then((res) => {
      // this.setState({ product: res.data.product });
      this.uploadingImagesIndex = this.uploadingImagesIndex.filter((index) => index !== uploadingIndex);
      this.uploadingImages = this.uploadingImages.filter((image ) => image.index !== uploadingIndex);
      this.setState({ loading: false });
      if (this.uploadingImagesIndex.length === 0 && this.uploadingImagesIndexError.length === 0) {
        if (shopDetails) {
          const productCategory = shopDetails.categories.filter((c) => c.id === res.data.product.category.id);
          console.log(productCategory);
          if (productCategory[0] && !productCategory[0].image) {
            if (user && user.user) {
              const data = { username: user.user.username };
              getShopDetails(data);
            }
          }
          this.updateStoreDetails(res.data.product);
        }
      }
    }).catch((err) => {
      this.uploadingImagesIndex = this.uploadingImagesIndex.filter((index) => index !== uploadingIndex);
      this.uploadingImagesIndexError = [...this.uploadingImagesIndexError, uploadingIndex];
      console.log(err);
      this.setState({ loading: false });

      if (err.response) {
        toast.error(err.response.error);
      } else {
        toast.error('An error ocurred in uploading the image');
      }
    });
  }

  retryImageUpload(imageIndex) {
    const imageFile = this.uploadingImages.find((image) => image.index === imageIndex);
    console.log('reuploading iamge index', imageIndex, 'image', imageFile);
    this.uploadingImagesIndexError = this.uploadingImagesIndexError.filter((index) => index !== imageIndex);
    this.onSelectFile(imageFile, true);
  }

  saveChanges(e) {
    if (e) {
      e.preventDefault();
    }

    if (e.target.id !== 'add-size-form') {
      const {
        product, update, sizes, textChanged,
      } = this.state;
      const {
        name, description, price, disc_price, estimated_delivery, id, cod_available, category, online_available, shipping_charges,
      } = product;
      const {
        products, setProducts, shopDetails, user, getShopDetails, publicPageProducts, setPublicPageProducts, chosenCategory, publicPageChosenCategory,
      } = this.props;
      let data = {
        name, cod_available, description, price: Number(price), disc_price: Number(disc_price), estimated_delivery, meta_data: { sizes_available: sizes }, stock: 10000000, online_available, shipping_charges: Number(shipping_charges), category,
      };

      if (!(online_available || cod_available)) {
        this.setState({ error: 'Enable the cod or online payment!!!' });
        return;
      }
      this.setState({ loading: true, error: '' });
      if (update) {
        data = { ...data, id };
        if (textChanged) {
          agent.ProductsPage.updateProduct(data).then((res) => {
            this.setState({
              addImage: true, loading: false, product: res.data.product, textChanged: false,
            });
            if (shopDetails && shopDetails.categories.filter((c) => c.id === res.data.product.category.id).length === 0) {
              if (user && user.user) {
                const data = { username: user.user.username };
                getShopDetails(data);
              }
            }
            this.updateStoreDetails(res.data.product);
          }).catch((err) => {
            this.setState({ loading: false });
            console.log('error in updating product ', err);
            if (err.response) {
              console.log('error in updating product ', err.response);
              toast.error(err.response.data.error);
            } else {
              toast.error('An error occured in updating product');
            }
          });
        } else {
          this.setState({ addImage: true, loading: false });
        }
      } else {
        agent.ProductsPage.createProduct(data).then((res) => {
          this.setState({ addImage: true, loading: false, product: res.data.product });
          if (!chosenCategory || chosenCategory === res.data.product.category.name) {
            setProducts({ products: [res.data.product, ...products] });
          }
          if (!publicPageChosenCategory || publicPageChosenCategory === res.data.product.category.name) {
            setPublicPageProducts({ products: [res.data.product, ...publicPageProducts] });
          }
          console.log(shopDetails.categories, res.data.product);
          if (shopDetails && shopDetails.categories.filter((c) => c.id === res.data.product.category.id).length === 0) {
            if (user && user.user) {
              const data = { username: user.user.username };
              getShopDetails(data);
            }
          }

          window.localStorage.setItem(IS_COD_AVAILABLE, res.data.product.cod_available);
          window.localStorage.setItem(ESTIMATED_DELIVERY, res.data.product.estimated_delivery);
          window.localStorage.setItem(IS_ONLINE_PAYMENT_AVAILABLE, online_available);
          window.localStorage.setItem(DELIVERY_CHARGE, shipping_charges);
        }).catch((err) => {
          console.log(err, err.response);
          this.setState({ loading: false });
          if (err.response) {
            toast.error(err.response.data.error);
          } else {
            toast.error('An error ocurred in creating product');
          }
        });
      }
    }
  }

  togelIsCreateProductDialog() {
    const { product, update } = this.state;
    if (!update) {
      const online_available = window.localStorage.getItem(IS_ONLINE_PAYMENT_AVAILABLE);
      const cod_available = window.localStorage.getItem(IS_COD_AVAILABLE);
      const estimated_delivery = window.localStorage.getItem(ESTIMATED_DELIVERY);
      const shipping_charges = window.localStorage.getItem(DELIVERY_CHARGE);
      this.setState({
        product: {
          online_available: (/true/i).test(online_available),
          cod_available: (/true/i).test(cod_available),
          estimated_delivery,
          shipping_charges,
        },
      });
    } else {
      this.setState({
        product,
      });
    }
    this.setState({
      isCreateProductDialog: !this.state.isCreateProductDialog,
      addImage: false,
    });
  }

  deleteImage(img) {
    const data = {
      image: img,
      product_id: this.state.product.id,
    };
    this.setState({ loading: true, error: '' });
    const {
      products, publicPageProducts, setProducts, setPublicPageProducts,
    } = this.props;
    agent.ProductsPage.deleteProductImage(data).then((res) => {
      this.setState({ loading: false });
      this.setState({ product: res.data.product });
      this.updateStoreDetails(res.data.product);
    }).catch((err) => {
      console.log(err);
      this.setState({ loading: false });

      if (err.response) {
        toast.error(err.response.error);
      } else {
        toast.error('An error ocurred in uploading the image');
      }
    });
  }

  deleteProduct() {
    const data = {
      product_id: this.state.product.id,
    };
    this.setState({ loading: true });
    const {
      products, publicPageProducts, setProducts, setPublicPageProducts,
    } = this.props;
    agent.ProductsPage.deleteProduct(data).then((res) => {
      const temp = products.filter((p) => p.id !== data.product_id);
      setProducts({ products: temp });
      const tempPublicPageProducts = publicPageProducts.filter((p) => p.id !== data.product_id);
      setPublicPageProducts({ products: tempPublicPageProducts });
      this.setState({ loading: false });
    }).catch((err) => {
      console.log(err);
      if (err.response) {
        toast.error(err.response.data.error);
      } else {
        toast.error('An error ocurred in deleting the product !!!');
      }
      this.setState({ loading: false });
    });
  }

  togelAddSizes() {
    this.setState({ addSizesVisible: !this.state.addSizesVisible });
  }

  isCodAvailableToggle() {
    this.setState({ product: { ...this.state.product, cod_available: !this.state.product.cod_available }, textChanged: true });
  }

  isOnlinePaymentAvailableToggle() {
    this.setState({ product: { ...this.state.product, online_available: !this.state.product.online_available }, textChanged: true });
  }

  addSize(e) {
    if (e) {
      e.preventDefault();
    }
    this.setState({ sizes: [...this.state.sizes, this.state.size], size: '', textChanged: true });
  }

  deleteSize(index) {
    const temp = this.state.sizes.filter((s, i) => i != index);
    this.setState({ sizes: temp, textChanged: true });
  }

  removeData(value) {
    if (value.id) {
      const data = { category_id: value.id };
      console.log('delete category', data);
      this.setState({ loading: true });
      const { shopDetails, setShopDetails } = this.props;
      agent.ProductsPage.deleteCategpry(data).then((res) => {
        const filteredCategories = shopDetails.categories.filter((c) => c.id !== value.id);
        const tempShopDetails = { ...shopDetails, categories: filteredCategories };
        setShopDetails(tempShopDetails);
        this.setState({ loading: false });
      }).catch((err) => {
        this.setState({ loading: false });
        console.log('Error in deleting category', err, err.response);
        if (err.response && err.response.data) {
          toast.error(err.response.data.error);
        } else {
          toast.error('Error in deleting category!!!');
        }
      });
    }
  }

  updateStoreDetails(product) {
    const {
      setProducts, products, user, publicPageProducts, setPublicPageProducts,
    } = this.props;
    const temp = products.map((p) => {
      if (p.id === product.id) {
        return product;
      }
      return p;
    });
    setProducts({ products: temp });
    const tempPublicPageProducts = publicPageProducts.map((p) => {
      if (p.id === product.id) {
        return product;
      }
      return p;
    });
    setPublicPageProducts({ products: tempPublicPageProducts });
  }

  render() {
    const {
      product, isCreateProductDialog, isNormalUser, update, addImage, addSizesVisible, sizes, size, error, ourCommission, textChanged, categories, loading,
    } = this.state;
    // console.log(product)
    return (
      <div>

        <button
          className=" center  flex add-new-product "
          onClick={this.togelIsCreateProductDialog.bind(this)}
        >
              Add new Product
        </button>

        <Dialog
          open={isCreateProductDialog}
          TransitionComponent={Transition}
          keepMounted
          maxWidth
          onClose={this.togelIsCreateProductDialog}
          aria-labelledby="alert-dialog-slide-title"
          aria-describedby="alert-dialog-slide-description"

        >
          {
                addImage
                  ? (
                    <div>
                      <DialogTitle id="alert-dialog-slide-title">Add Product Image</DialogTitle>

                      <DialogContent style={{
                        flexWrap: 'wrap', position: 'relative', display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center',
                      }}
                      >
                        <div className="dyna-flex flex-wrap ">
                          <div className="product-img-add-img-btn relative">
                            <AddPhotoAlternateIcon />
                            <SelectImage onSelectFile={this.onSelectFile.bind(this)} />
                          </div>
                          {
                              product.images.map((img, i) => (
                                <div className="product-admin-img-div" key={i}>
                                  {
                                    this.uploadingImagesIndex.includes(i)
                                      ? (
                                        <div className="uploding-loader">
                                          <h3 className='' >Uploading ...</h3>
                                          <CircularProgress style={{ width: '40px', height: '40px', color: 'white' }} />
                                        </div>
                                      )
                                      : (
                                        <>

                                          {
                                        this.uploadingImagesIndexError.includes(i)
                                          ? (
                                            <div className="uploding-loader">
                                              <h3 className='red f4 b' >Error Occurred</h3>
                                              <button className="retry-button" onClick={() => this.retryImageUpload(i)}> Retry </button>
                                            </div>
                                          ) : <button className="" onClick={() => this.deleteImage(img)} type="button">

                                          <DeleteForeverIcon />
                                        </button>
      
                                      }
                                        </>
                                      )
                                  }
                                  <Image src={img} alt="" overlaySrc={product.preview_images[i]} />
                                  
                                </div>
                              ))
                          }

                        </div>


                      </DialogContent>
                      <DialogActions>
                        <button type="submit" color="primary" className="color-btn ma1 relative w-50">
                              Add Image
                          <SelectImage onSelectFile={this.onSelectFile.bind(this)} />
                        </button>
                        <button onClick={this.togelIsCreateProductDialog} color="primary" className="btn ba w-50">
                          Done
                        </button>

                      </DialogActions>
                    </div>
                  )
                  : (

                    <form onSubmit={this.saveChanges.bind(this)} className="create-product-form">
                      <DialogTitle id="alert-dialog-slide-title">Create Product</DialogTitle>
                      <DialogContent style={{
                        flexWrap: 'wrap', position: 'relative', display: 'flex', flexDirection: 'column', alignItems: 'center',
                      }}
                      >
                        <TextInput label="Product Name" value={product.name} type="text" className="w-100" onTextChange={(text) => this.setState({ textChanged: true, product: { ...product, name: text } })} required />
                        <DropDownTextInput label="Add Product Category" data={categories} onChangeText={(text) => this.setState({ textChanged: true, product: { ...product, category: text } })} setOption={(value) => this.setState({ textChanged: true, product: { ...product, category: value.name } })} className="w-100" canRemoveData removeData={this.removeData} value={product.category} />
                        <TextArea label="Description" value={product.description} type="text" className="w-100" onTextChange={(text) => this.setState({ textChanged: true, product: { ...product, description: text } })} required rows={4} />
                        <TextInput label="Price" value={product.price} type="number" className="w-100" onTextChange={(text) => this.setState({ textChanged: true, product: { ...product, price: text } })} required />
                        <TextInput label="Discounted Price" value={product.disc_price} className="w-100" type="number" onTextChange={(text) => this.setState({ textChanged: true, product: { ...product, disc_price: text } })} required />
                        {/* <TextInput label="Stock" value={product.stock} type="number" className="w-100" onTextChange={(text) => this.setState({textChanged: true, product: { ...product, stock: text } })} required /> */}
                        <TextInput label="Expected Delivery (in days)" value={product.estimated_delivery} type="text" className="w-100" onTextChange={(text) => this.setState({ textChanged: true, product: { ...product, estimated_delivery: text } })} required />
                        <TextInput label="Shipping charge(in Rs.)" value={product.shipping_charges} type="number" className="w-100" onTextChange={(text) => this.setState({ textChanged: true, product: { ...product, shipping_charges: text } })} required />

                        <div className="flex items-center ">
                          <label> Sizes Available on Product : </label>
                          <Switch togelswitch={this.togelAddSizes.bind(this)} val={addSizesVisible} />
                        </div>
                        {
                          addSizesVisible ? (
                            <form onSubmit={this.addSize.bind(this)} id="add-size-form" className="w-100">
                              <div className="flex w-100 items-center  ">
                                <TextInput label="Add Size" value={size} type="text" className="w-60" onTextChange={(text) => this.setState({ size: text })} required />
                                <button type="submit" className="color-btn w-40 ">Add Size</button>
                              </div>
                            </form>
                          ) : null
                        }

                        <div className="flex flex-wrap">
                          {
                            sizes.map((s, i) => (
                              <div className="size" key={i}>
                                <p>
                                  {s}
                                </p>
                                <DeleteOutlineIcon onClick={() => this.deleteSize(i)} />
                              </div>
                            ))
                          }
                        </div>

                        <div className="flex items-center ">
                          <label> Cash On Delivery Available: </label>
                          <Switch togelswitch={this.isCodAvailableToggle.bind(this)} val={product.cod_available} />

                        </div>
                        {
                            product.cod_available
                              ? <small className="green" style={{ fontSize: '0.7rem' }}>{`You will be charged ${ourCommission.cod.percent}% + ${ourCommission.cod.extra} Rs. on each order`}</small>
                              : null
                          }
                        <div className="flex items-center ">
                          <label> Online Payment Available: </label>
                          <Switch togelswitch={this.isOnlinePaymentAvailableToggle.bind(this)} val={product.online_available} />

                        </div>
                        {
                            product.online_available
                              ? <small className="green" style={{ fontSize: '0.7rem' }}>{`You will be charged ${ourCommission.online.percent}% + ${ourCommission.online.extra} Rs. on each order`}</small>
                              : null
                          }
                        <p className="err-txt">{error}</p>
                      </DialogContent>
                      <DialogActions>
                        {
                              update
                                ? (
                                  <div>

                                    <button type="submit" color="primary" className="color-btn ma1 " style={{ flexGrow: 1 }}>
                                      {
                                        textChanged
                                          ? (
                                            'Save Changes'
                                          )
                                          : (
                                            'Add Images'
                                          )
                                      }
                                    </button>
                                    <button type="button" onClick={() => this.deleteProduct()} color="primary" className="color-btn ma1 " style={{ flexGrow: 1 }}>
                                    Delete Product
                                    </button>
                                  </div>
                                )
                                : (
                                  <button type="submit" color="primary" className="color-btn ma1 " style={{ flexGrow: 1 }}>

                                    {
                                        textChanged
                                          ? (
                                            'Save Changes'
                                          )
                                          : (
                                            'Add Images'
                                          )
                                      }
                                  </button>
                                )

                          }

                        <button onClick={this.togelIsCreateProductDialog} color="primary" className="btn ba ">
                          Cancel
                        </button>

                      </DialogActions>
                    </form>

                  )
            }

            {
              loading? 
              <Loader/>:
              null
            }
          {
            isNormalUser
              ? (
                <DisabledDiv message="Purchase a pro pack to get this utility." />
              ) : null
          }
        </Dialog>
      </div>

    );
  }
}


const mapStateToProps = (state) => ({
  user: state.AuthReducer.user,
  products: state.ProductsPageReducers.products,
  shopDetails: state.ProductsPageReducers.shopDetails,
  publicPageProducts: state.HomePageReducers.publicPageProducts,
  chosenCategory: state.ProductsPageReducers.chosenCategory,
  publicPageChosenCategory: state.HomePageReducers.publicPageChosenCategory,
  // categories: state.ProductsPageReducers.categories,
});

const mapDispatchToProps = (dispatch) => ({
  setProducts: (products) => dispatch({ type: SET_PRODUCTS, payload: products }),
  setPublicPageProducts: (products) => dispatch({ type: SET_PUBLIC_PAGE_PRODUCTS, payload: products }),

  getShopDetails: (user) => dispatch({ type: GET_SHOP_DETAILS, payload: agent.ProductsPage.getShopDetails(user) }),
  setShopDetails: (shopDetails) => dispatch({ type: GET_SHOP_DETAILS, payload: shopDetails }),

  // getShopDetails: (user) => dispatch({ type:  payload: agent.ProductsPage.getShopDetails(user) }),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(AdminPage);
